.centrado{
    text-align: center;
} 

.background{
    background-color: #303a52;
    color: white;
} 

.table-head{
    background-color: #4592af;
    color: white; 
    font-size: large;
    font-weight: bolder;
}


.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #09f;
  
    animation: spin 1s ease infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }
